import {Message} from 'element-ui'

const date = new Date().getFullYear() + '-' + (new Date().getMonth() + 1) + '-' + new Date().getDate() + ' ' + new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds() + ':' + new Date().getMilliseconds()

/*
  *  
  *  customerName 
  *  fileName +
 */
export function businessUpload(customerName, fileName) {
  if (customerName == '' || customerName == undefined) {
    Message({message: '', type: 'error', duration: 5 * 1000});
    return;
  }
  if (fileName == '' || fileName == undefined) {
    Message({message: '', type: 'error', duration: 5 * 1000});
    return;
  }
  let filePath = '/' + customerName + '//' + fileName + '_' + date;

  return filePath;
}

