<template>
  <div class="upload-container">
    <el-button icon='el-icon-upload' size="mini" :style="{background:color,borderColor:color}" @click=" dialogVisible=true" type="primary">PDF
    </el-button>
    <el-dialog append-to-body
    :visible.sync="dialogVisible">
      <div class="upload_button">
        <el-upload
          class="upload-demo"
          :action="host + '/core/upload-pdf-img-to-oss/'"
          :on-remove="handleRemove"
          :on-success="handleSuccess"
          :before-upload="beforeUpload"
          v-loading="loading"
          target=".upload_button"
          element-loading-text=""
          :file-list="fileList">
          <el-button size="small" type="primary"></el-button>
        </el-upload>
      </div>
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="handleSubmit"> </el-button>
    </el-dialog>
  </div>
</template>

<script>
// import { getToken } from 'api/qiniu'

export default {
  name: 'editorSlideUpload',
  props: {
    color: {
      type: String,
      default: '#1890ff'
    }
  },
  data() {
    return {
      host: process.env.BASE_API,
      dialogVisible: false, // 
      fileList: [], // 
      imgList: [], // PDF
      loading: false // 
    }
  },
  methods: {
    // 
    handleSubmit() {
      // imgList
      this.$emit('handlePDFSuccess', this.imgList)
      // 
      this.imgList = []
      this.fileList = []
      this.dialogVisible = false
    },
    // 
    handleSuccess(response, file) {
      if (response.code === 1) {
        this.imgList = response.data.img_list
        // 
        this.imgList = this.imgList.map(v => {
          // return this.host + v
          return v
        })
        this.$message({
          type: 'success',
          message: ''
        })
        this.loading = false
      } else {
        this.$message({
          type: 'error',
          message: response.message
        })
      }
    },
    // 
    handleRemove(file, fileList) {
      this.imgList = []
      this.fileList = []
    },
    // pdf
    beforeUpload(file) {
      if (file.type !== 'application/pdf') {
        this.$message({
          type: 'error',
          message: 'PDF,'
        })
        return
      }
      this.loading = true
    }
  }
}
</script>

<style lang="less" scoped>
.upload_button {
    text-align: center;
    padding: 50px 0;
    margin: 20px;
    border: 1px dashed #ccc;
}
</style>



