import request from '@/utils/request'
import requestLoading from '@/utils/requestLoading'

const pre = '/crm'

// 
export function queryAllOrder(data) {
  return requestLoading({
    url: pre + '/api/crm/order/queryAllOrder',
    method: 'post',
    data
  })
}

